@import "bootstrap-variables";
@import "bootstrap-custom";
@import "slick.scss";

@font-face {
	font-family: "Proxima Nova";
	src: url(data:font/opentype;base64,);
	font-style: normal;
	font-weight: 800;
}

@font-face {
	font-family: "Proxima Nova";
	src: url(data:font/opentype;base64,);
	font-style: normal;
	font-weight: 600;
}
@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i');

h1,h2,h3,h4,h5,h6 {
	font-family: "Proxima Nova", Arial, sans-serif;
	text-transform: uppercase;
}

body {
	font: 400 20px/1.3 'PT Sans', Arial, sans-serif;;
	margin: 0;
	// padding-bottom: 200px;
	background-color:  #ebebeb;
	color: #665f60;
}
img{
	max-width: 100%;
	height: auto;
}
.ba-section-title {
	font-size: 36px;
	color: currentColor;
	font-weight: 600;
	text-align: center;
	margin-bottom: 50px;
}
.ba-section-btn {
	display: inline-block;
	font-size: 20px;
	font-style: italic;
	padding: 20px 50px;
	border: 1px solid;
	color: #7d7a7c;
	&--white {
		border: 1px solid #fff;
		color: #FFF;
	}
	&:hover {
		text-decoration: none;
		color: currentColor;
		background: #7dfa7c;
	}
}
.ba-header {
	@media (max-width: $screen-xs-max) {
		.ba-nav {
			width: 100%;
		}
		.ba-header__title {
			margin-top: 150px;
		}
	}
	background: #fff url(../img/header-bg.jpg) no-repeat;
	background-size: cover;
	min-height: 100vh;
	padding: 35px 0;
	color: #FFF;
	text-align: center;
	&__title {
		margin-top: 300px;
		font: 700 72px/1.3 "Proxima Nova", Arial, sans-serif;
	}
	&__subtitle {
		font-size: 30px;
		font-style: italic;
		font-weight: 400;
	}
}
.ba-nav {
	text-transform: uppercase;
	float: right;
	line-height: 72px;
	&__item {
		float: left;
		margin-left: 20px;
		list-style-type: none;
		a {
			font-size: 24px;
			font-weight: 600;
			color: #FFF;
			text-decoration: none;
			&:hover {
				color: #FF2;
			}
		}
	}
}

.ba-about-us {
	@media (max-width: $screen-xs-max) {
		text-align: center;
		.ba-about-us__content {
			margin-top: 40px;
			margin-left: 0;
		}
	}
	padding-top: 130px;
	&__img {
		position: relative;
	}
	&__icon {
		position: absolute;
		top: 0;
		left: 50%;
	}
	&__content {
		margin-left: 100px;
		// text-align: justify;
		text-align: center;
	}
	.ba-about-us__learn-btn {
		text-decoration: none;
		margin-top: 100px;
	}
}

.ba-our-jobs {
	@media (max-width: $screen-xs-max) {
		.ba-our-jobs__left{
			display: block;
		}
		.ba-our-jobs__right {
			// flex-order: 1;
			display: block;
		}
		.ba-our-jobs__item {
			// flex-basis: 100%;
			display: block;
		}
	}
	display: flex;
	text-align: center;
	padding-top: 100px;
	&__left {
		display: flex;
		flex-basis: 66.66%;
		flex-wrap: wrap;
	}
	&__right {
		flex-basis: 33.33%;
		background-color: #9E99CD;
		color: #fff;
	}
	&__item {
		flex-basis: 50%;
		padding: 100px 20px;
		&--bg-gray {
			background-color: #DADADA;
		}
		&-services {
			background: #9E99CD url(../img/our-services-bg.jpg) no-repeat;
			background-size: cover;
			height: 100%;
			background-position: 100% 50%;
		}
		&-services-title {
			font: 700 36px/1.3 "Proxima Nova", Arial, sans-serif;
		}
		&-services-content {
			font-size: 18px;
			font-style: italic;
		}
	}
	&__item-title {
		font-size: 24px;
		font-weight: 700;
	}
}

.ba-recent-works {
	padding: 50px;
	background-color: #B5B3D4;
	text-align: center;
	color: #fefcfe;
	&__icon {
		margin-bottom: 20px;
	}
}
.ba-first-slider {
	position: relative;
	&__item {
		img {
			margin: 0 auto;
		}
		text-align: center;
	}
}
.slick-dots {
	padding-left: 0;
	margin: 30px 0;
	li {
		display: inline-block;
		background: transparent;
		color: #FFF;
		margin-left: 10px;
		border-radius: 50%;
		border: 1px solid #ebebeb;
		button {
			display: block;
			width: 10px;
			height: 10px;
			padding: 0;
			background: none;
			border: none;
			font-size: 0;
		}
	}
	.slick-active {
		background-color: #FFF;
	}
}
.ba-first-slider__prev,
.ba-first-slider__next  {
	position: absolute;
	transform: translateY(-50%);
	top: 50%;
}
.ba-first-slider__prev {
	left: 0;
}
.ba-first-slider__next {
	right: 0;
}

.ba-our-team {
	text-align: center;
	padding: 50px 0;
	@media (max-width: $screen-xs-max) {
		.ba-second-slider__item {

		}
	}
}

.ba-second-slider {
	position: relative;
	&__item {
		padding: 10px 40px;
		img {
			margin: 0 auto;
		}
		text-align: center;
	}
}
.ba-second-slider__prev,
.ba-second-slider__next  {
	position: absolute;
	transform: translateY(-50%);
	top: 50%;
}
.ba-second-slider__prev {
	left: 0;
}
.ba-second-slider__next {
	right: 0;
}
.ba-social-links {
	padding: 0;
	background: #9E99CD;
	text-align: center;
	&__item {
		padding: 10px;
		list-style-type: none;
		display: inline-block;
		margin-left: 10px;
	}
}
.ba-person {
	&__name {
		font-family:"Proxima Nova", Arial, sans-serif;
		font-size: 24px;
		color: #9f9acf;
		margin-bottom: 20px;
	}
	&__position {
		font-size: 24px;
		font-style: italic;
		color: #665f60;
		margin-bottom: 20px;
	}
}

.ba-footer {
	display: flex;
	align-items: center;
	height: 180px;
	color: #fff;
	background-color: #9E99CD;
	&__copy {
		margin-bottom: 0;
	}
	.ba-social-links {
		background: transparent;
		margin: 0;
	}
}
.ba-map {
	background-color: #9E99CD;
	text-align: center;
	padding: 100px;
	margin-bottom: 10px;
}
